:root {
  --colour-text: #1b1718;
  --colour-grey: #f1f1ee;
  --colour-lightgrey: #95a3a9;
  --colour-border: #f1f1ef;
  --colour-black: var(--colour-text);
  --colour-bg: white;
  --colour-red: #c60;
  --colour-green: #033;
  --colour-blue: #0f5178;
  --font-head: "marritalic", serif;
  --font-body: "marrthinitalic", serif;
  --font-druk: "Druk Cond Super", Baskerville, Baskerville Old Face, Hoefler Text, Garamond, Times New Roman, serif;
  --font-head-weight: normal;
  --font-head-style: italic;
  --font-body-weight: 300;
  --font-body-style: italic;
  --font-druk-weight: normal;
  --font-druk-style: normal;
  --transit: .4s all cubic-bezier(.32, .24, .15, 1);
  --transit-delay: .4s all cubic-bezier(.32, .24, .15, 1) .3s;
}

body {
  font-family: var(--font-body);
  font-weight: var(--font-body-weight);
